import { stub } from 'sinon';
import { AttributeType } from './AttributeType';
import { PropertyMock } from './Property.mock';
import { NumberMetadataMock } from '../Metadata';
export class NumberPropertyMock extends PropertyMock {
  constructor(propertyName, db, entityMetadata) {
    super(db, entityMetadata.LogicalName, propertyName);
    this._SetValue = void 0;
    this.attributes = void 0;
    this.raw = void 0;
    this.raw = null;
    this._SetValue = stub();
    this._SetValue.callsFake(value => {
      this._db.UpdateValue(value, this._boundTable, this._boundColumn, this._boundRow);
    });
    this._Refresh.callsFake(() => {
      const {
        value,
        attributeMetadata
      } = this._db.GetValueAndMetadata(this._boundTable, this._boundColumn, this._boundRow);
      if (!(attributeMetadata.AttributeType === AttributeType.Integer || attributeMetadata.AttributeType === AttributeType.BigInt || attributeMetadata.AttributeType === AttributeType.Decimal || attributeMetadata.AttributeType === AttributeType.Double)) {
        throw new Error('Type Error');
      }
      this.attributes = new NumberMetadataMock();
      this.attributes.LogicalName = attributeMetadata.LogicalName;
      this.attributes.DisplayName = attributeMetadata.DisplayName;
      this.attributes.MaxValue = attributeMetadata.MaxValue;
      this.attributes.MinValue = attributeMetadata.MinValue;
      this.raw = value;
      this.formatted = value === null || value === undefined ? '' : '' + value;
    });
    const attribute = {
      AttributeType: AttributeType.Decimal,
      EntityLogicalName: entityMetadata.LogicalName,
      LogicalName: propertyName
    };
    entityMetadata.Attributes?.push(attribute);
  }
}